

import React, { Component } from "react";

import "./index.scss";

import Head from "&/components/head";
export default class Scan extends Component {

    
    render() {
        return (
            <div className="scan">
                <Head title="扫一扫" show={true} scan={true} location={this.props.location}></Head>
              
            </div>
        )
    }
}